import Vue from 'vue'
import moment from 'moment'
moment.locale('zh-cn');
// 过滤时间戳
Vue.filter('filterTime',time=>{
      return moment(time*1000).format('YYYY-MM-DD')
  })
// 过滤时间戳
Vue.filter('filterTimeMMDD',time=>{
    let onNowDate = moment(new Date().getTime()).format('MM-DD').split('-').join('')
    let onTileDate = moment(time*1000).format('MM-DD').split('-').join('')
    let onMonDate = moment(time*1000).format('MM-DD').split('-').join('月')+'日'
    let onWeekDate = moment(time*1000).format('dddd').split('星期').join('周')
    if(onNowDate === onTileDate){
        return '今天'+onMonDate
    }else if(onNowDate == onTileDate-1){
        return '明天'+onMonDate
    }else if(onNowDate == onTileDate-2){
        return '后天'+onMonDate
    }else{
        return onWeekDate+onMonDate

    }
  })

// 去掉后两位
Vue.filter('abandon',NumStr=>{
    NumStr = NumStr.toString()
    let oStr = NumStr.substring(0,2)
        if(NumStr[2] != 0){
            oStr = oStr+'.'+NumStr[2]
        }
    return oStr
})

// 过滤演职人员
Vue.filter('ActorFilter',action=>{
    if(action){
        action = action.map(item=>{
            return item.name
        })
        return action.toString()
    }else{
        return
    }
})
