import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

let routes = [
    {
        path:'/cinema',
        component:()=>import('@/views/cinema.vue'),
        children:[
            {
                path:'/cinema',
                redirect:'nowplaying'
            },
            {
                path:'nowplaying',
                component:()=>import('@/views/Cinema/nowplaying')
            },
            {
                path:'comingsoon',
                component:()=>import('@/views/Cinema/comingsoon')
            },
        ]
    },
    {
        path:'/detils/:id',
        component:()=>import('@/views/detils.vue'),
    },
    {
        path:'/cinemaHome',
        component:()=>import('@/views/cinemaHome.vue'),
    },
    {
        path:'/cinemaDetils/:CinemaId',
        component:()=>import('@/views/cinemaDetils.vue'),
    },
    {
        path:'/city',
        component:()=>import('@/views/city.vue'),
    },
    {
        path:'/search',
        component:()=>import('@/views/search.vue'),
    },
    {
        path:'/information',
        component:()=>import('@/views/information.vue'),
    },
    {
        path:'/center',
        component:()=>import('@/views/center.vue'),
    },
    {
        path:'/login',
        component:()=>import('@/views/login.vue'),
    },
    {
        path:'/',
        redirect:'/cinema'
    },
    {
        path:'*',
        redirect:'/cinema'
    },
]

let router = new VueRouter({
    routes
})

export default router