import Swiper from "swiper";
import Vue from "vue";

//自定义标签 轮播图
Vue.directive('swiper', {
    inserted(el, binding) {
        if (binding.value.index === binding.value.datalist.length - 1) {
            new Swiper('.' + binding.value.name, {
                slideToClickedSlide: true,
                slidesPerView: binding.value.num,
                spaceBetween: binding.value.space,
                centeredSlides: binding.value.centeredSlides ? true : false,

                effect:  binding.value.centeredSlides ? 'coverflow' : '',
                grabCursor:  binding.value.centeredSlides ? true : false,
                coverflowEffect: binding.value.centeredSlides ? {
                    rotate: 50,
                    stretch: 0,
                    depth: 100,
                    modifier: 1,
                    slideShadows: true,
                } : false, 
            })
        }
    }
})

// 自定义标签 头部根据滚动条显隐
Vue.directive('scrollHeight', {
    inserted(el, binding) {
        el.style.opacity = 0
        window.onscroll = function () {
            let oH = document.documentElement.scrollTop || document.body.scrollTop
            if (oH > (binding.value || 50)) {
                el.style.opacity = 1
            } else {
                el.style.opacity = 0
            }
        }
    }
})